// 完成
<template>
  <div class="index">
    <h2>剧目管理</h2>
    <!-- 查询 -->
    <div class="search">
      <div class="inp">
        <el-form :inline="true" label-position="left">
          <el-form-item label="剧目名称:" prop="searchName">
            <el-input v-model="searchName"></el-input>
          </el-form-item>
          <el-button @click="search()">查询</el-button>
          <el-button @click="insert()">增加</el-button>
          <div class="clear"></div>
        </el-form>
      </div>
    </div>
    <!-- 展示 -->
    <div class="show">
      <h3>剧目展示</h3>
      <table>
        <thead>
          <tr>
            <th>剧目编号</th>
            <th>剧目海报</th>
            <th>剧目名称</th>
            <th>剧目类型</th>
            <th>上映时间</th>
            <th>电影时间</th>
            <th>剧目描述</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in this.playDate" :key="index">
            <td>{{ index + 1 }}</td>
            <td><img :src="item.imgUrl" alt="海报" /></td>
            <td>{{ item.name }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.time }}</td>
            <td>{{ item.duration }}</td>
            <td>{{ item.description }}</td>
            <td>
              <button @click="editPlay(index)">修改</button>
              <button @click="deletePlay(index)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- 添加 -->
    <div class="addForm">
      <el-form
        v-show="addPlay"
        :model="selectedPlay"
        status-icon
        label-width="100px"
      >
        <h3 class="Title">添加剧目</h3>
        <el-form-item label="剧目名称" prop="selectedPlay.name">
          <el-input v-model="selectedPlay.name"></el-input>
        </el-form-item>
        <el-form-item label="剧目海报" prop="selectedPlay.imgUrl">
          <el-input v-model="selectedPlay.imgUrl"></el-input>
        </el-form-item>
        <el-form-item label="上映时间" prop="selectedPlay.time">
          <el-input v-model="selectedPlay.time"></el-input>
        </el-form-item>
        <el-form-item label="剧目类型" prop="selectedPlay.type">
          <el-input v-model="selectedPlay.type"></el-input>
        </el-form-item>
        <el-form-item label="剧目时长" prop="selectedPlay.duration">
          <el-input v-model="selectedPlay.duration"></el-input>
        </el-form-item>
        <el-form-item label="剧目评分" prop="selectedPlay.score">
          <el-input v-model="selectedPlay.score"></el-input>
        </el-form-item>
        <el-button type="primary" @click="submitAdd()">提交添加</el-button>
        <el-button type="primary" @click="cancleAdd()">取消</el-button>
      </el-form>
    </div>
    <!-- 修改 -->
    <div class="updateForm">
      <el-form
        v-show="updatePlay"
        :model="selectedPlay"
        status-icon
        label-width="100px"
      >
        <h3 class="Title">修改剧目信息</h3>
        <el-form-item label="剧目id" prop="selectedPlay.id">
          <el-input v-model="selectedPlay.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="剧目名称" prop="selectedPlay.name">
          <el-input v-model="selectedPlay.name"></el-input>
        </el-form-item>
        <el-form-item label="剧目海报" prop="selectedPlay.imgUrl">
          <el-input v-model="selectedPlay.imgUrl"></el-input>
        </el-form-item>
        <el-form-item label="上映时间" prop="selectedPlay.releaseTime">
          <el-input v-model="selectedPlay.releaseTime"></el-input>
        </el-form-item>
        <el-form-item label="剧目类型" prop="selectedPlay.type">
          <el-input v-model="selectedPlay.type"></el-input>
        </el-form-item>
        <el-form-item label="剧目时长" prop="selectedPlay.duration">
          <el-input v-model="selectedPlay.duration"></el-input>
        </el-form-item>
        <el-form-item label="剧目评分" prop="selectedPlay.score">
          <el-input v-model="selectedPlay.score"></el-input>
        </el-form-item>
        <el-button type="primary" @click="savePlay()">提交修改</el-button>
        <el-button type="primary" @click="cancleSave()">取消</el-button>
      </el-form>
    </div>
    <div class="clear"></div>
  </div>
</template>
<script>
export default {
  name: "PlayManagement",
  data() {
    return {
      searchName: "",
      playDate: [],
      updatePlay: false,
      addPlay: false,
      selectedPlay: {
        id: "",
        name: "",
        imgUrl: "",
        type: "",
        duration: "",
        time: "",
        description: "",
        score: "",
      },
    };
  },
  async created() {
    const { data: res } = await this.$http.get(`/video/getVideo`, {
      params: {
        pageByTotalNum: 10,
        page: 1,
      },
    });
    this.playDate = res.value.object;
    console.log(this.playDate);
  },
  methods: {
    async search() {
      const { data: res } = await this.$http.get("/video/getVideoByName", {
        params: {
          pageByTotalNum: "6",
          page: "1",
          name: this.searchName,
        },
      });
      this.playDate = res.value.object;
    },
    // 显示添加表单
    async insert() {
      this.addPlay = true;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "airblue";
      a.style.border = "1px solid blueviolet";
      a.style.boxShadow = "0 0 25px #f885ff";
    },
    // 提交添加修改
    async submitAdd() {
      // 请求
      const { data: res } = await this.$http.post(`video/insertVideo`, {
        token: window.localStorage.getItem("token"),
        value: {
          name: this.selectedPlay.name,
          imgUrl:
            "https://p0.pipi.cn/mmdb/fb738687e7addd7df5030c82a13f15b023a9c.jpg?imageView2/1/w/218/h/300",
          time: this.selectedPlay.time,
          type: this.selectedPlay.type,
          duration: this.selectedPlay.duration,
          score: this.selectedPlay.score,
        },
      });
      console.log(res);
      // 插入后知更新
      const { data: rest } = await this.$http.get("/video/getVideo", {
        params: {
          pageByTotalNum: 10,
          page: 1,
        },
      });
      console.log(rest);
      this.playDate = rest.value.object;
      this.addPlay = false;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    cancleAdd() {
      this.addPlay = false;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    // 更新表单可见
    async editPlay(index) {
      this.updatePlay = true;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "airblue";
      a.style.border = "1px solid blueviolet";
      a.style.boxShadow = "0 0 25px #f885ff";
      this.selectedPlay = this.playDate[index];
    },
    //提交更新表单
    async savePlay() {
      const { data: res } = await this.$http.post(`/video/updateVideo`, {
        token: window.localStorage.getItem("token"),
        value: {
          id: this.selectedPlay.id,
          name: this.selectedPlay.name,
          imgUrl:
            "https://p0.pipi.cn/mmdb/fb738687e7addd7df5030c82a13f15b023a9c.jpg?imageView2/1/w/218/h/300",
          time: this.selectedPlay.time,
          type: this.selectedPlay.type,
          duration: this.selectedPlay.duration,
          score: this.selectedPlay.score,
        },
      });
      const { data: rest } = await this.$http.get("/video/getVideo", {
        params: {
          pageByTotalNum: 10,
          page: 1,
        },
      });
      this.playDate = rest.value.object;
      this.updatePlay = false;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    cancleSave() {
      this.updatePlay = false;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    // 删除剧目
    async deletePlay(index) {
      this.selectedPlay = this.playDate[index];
      const { data: res } = await this.$http.get("/video/deleteVideo", {
        params: {
          id: this.selectedPlay.id,
          token: window.localStorage.getItem("token"),
        },
      });
      const { data: rest } = await this.$http.get("/video/getVideo", {
        params: {
          pageByTotalNum: 10,
          page: 1,
        },
      });
      this.playDate = rest.value.object;
    },
  },
};
</script>
<style scoped>
.index {
  position: relative;
  padding: 20px;
}
h2 {
  text-align: center;
}
.search .el-form-item {
  float: left;
  width: 200px;
}
.search .el-button {
  float: left;
  width: 60px;
  text-align: center;
  border-radius: 5px;
  margin-top: 40px;
}
.show {
  width: 60%;
  margin: 0 auto;
}
.show table tr {
  width: 100%;
}
.show table th,
.show table td {
  text-align: center;
  width: 100px;
  height: 40px;
}
.show table td img {
  width: 40px;
  height: 40px;
}
.show table td button {
  width: 48px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  margin-right: 2px;
}
/* 添加表单 */
.addForm {
  float: left;
  width: 350px;
  height: 550px;
  margin: 10px 100px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
}
/* 修改表单 */
.updateForm {
  float: right;
  width: 350px;
  height: 550px;
  margin: 10px 100px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
}
.el-button {
  float: left;
  width: 100px;
  text-align: center;
  border-radius: 5px;
  margin: 10px 30px 0 0;
}
</style>