// 完成
<template>
  <div style="width: 100%">
    <!-- 顶部 -->
    <div class="top">
      <div class="time">
        <span>{{ this.todayDate }}</span>
        &nbsp;&nbsp;&nbsp;
        <span style="color: green">已更新</span>
      </div>
      <div class="rule">
        榜单规则<b>:</b>
        &nbsp;&nbsp;将昨日热映的影片,按照评分高低取其中的前10名,每天凌晨00:00更新
      </div>
    </div>
    <!-- 展示 -->
    <div class="show">
      <div
        class="film"
        v-for="(item, index) in this.film"
        :key="index"
        @click="turnToMovieDetails(item)"
      >
        <div class="head">{{ index + 1 }}</div>
        <div class="img"><img :src="item.imgUrl" alt="图片" /></div>
        <div class="tail">
          <div class="name">{{ item.name }}</div>
          <div class="actors">主演：{{ item.actors }}</div>
          <!-- item.time -->
          <div class="time">上映时间：{{ item.time }}</div>
        </div>
        <div class="score">
          <!-- item.score -->
          <div class="integer">{{ parseInt(item.score) + "." }}</div>
          <div class="decimal">{{ ("" + (item.score % 1))[2] }}</div>
          <div class="clear"></div>
        </div>
        <div class="clear"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "HistoricalRankings",
  data() {
    return {
      todayDate: "",
      film: [],
    };
  },
  async created() {
    const { data: data } = await this.$http.get(`/video/getVideoByScore`, {
      params: {
        pageByTotalNum: 10,
        page: 1,
        order: 0,
      },
    });
    console.log(data);
    this.film = data.value.object;
    for (let i = 0; i < this.film.length; i++) {
      let temp2 = "";
      temp2 += new Date(this.film[i].time).getFullYear() + "-";
      temp2 +=
        new Date(this.film[i].time).getMonth() + 1 > 10
          ? new Date(this.film[i].time).getMonth() + 1 + "-"
          : "0" + (new Date(this.film[i].time).getMonth() + 1) + "-";
      temp2 +=
        new Date(this.film[i].time).getDate() > 10
          ? new Date(this.film[i].time).getDate()
          : "0" + new Date(this.film[i].time).getDate();
      this.film[i].time = temp2;
    }
    // 获取当前时间
    var temp = new Date();
    this.todayDate += temp.getFullYear() + "-";
    this.todayDate += temp.getMonth() + 1;
    this.todayDate += "-" + temp.getDate();
  },
  methods: {
    turnToMovieDetails(item) {
      window.localStorage.setItem("movieDetails", JSON.stringify(item));
      this.$router.push("/MovieDetails");
    },
  },
};
</script>
<style scoped>
/* 头部 */
.top {
  height: 100px;
  margin-top: 50px;
  font-size: 12px;
  line-height: 20px;
}
.top .time {
  height: 20px;
}
.top .rule {
  height: 20px;
}
/* 展示 */
.show .film {
  width: 60%;
  height: 220px;
  margin: 10px auto 0;
}
.show .film .head {
  float: left;
  width: 50px;
  height: 50px;
  line-height: 50px;
  background-color: red;
  margin: 85px 20px;
}
.show .film .img {
  float: left;
}
.show .film .img img {
  width: 160px;
  height: 220px;
}
/* 描述 */
.show .film .tail {
  float: left;
  width: 400px;
  height: 220px;
  margin-left: 20px;
  text-align: left;
  border-bottom: 1px solid #e5e5e5;
}
.show .film .tail .name {
  font-size: 26px;
  margin: 80px 0 10px 0;
  color: #333333;
}
.show .film .tail .actors {
  font-size: 16px;
  margin: 4px;
  color: #333333;
}
.show .film .tail .time {
  color: #999999;
  font-size: 16px;
}
.show .film .score {
  float: left;
  width: 200px;
  height: 220px;
  padding-top: 60px;
  box-sizing: border-box;
  border-bottom: 1px solid #e5e5e5;
}
.show .film .score .integer {
  float: left;
  font-size: 56px;
}
.show .film .score .decimal {
  float: left;
  font-size: 26px;
  padding-top: 28px;
  box-sizing: border-box;
  /* vertical-align: bottom; */
}
</style>