// 检票管理
<template>
  <div>
    <div class="content">
      <div class="add">
        <el-button type="text" @click="dialog = true">添加验票管理</el-button>
        <el-drawer
          title="添加验票管理"
          :before-close="handleClose"
          :visible.sync="dialog"
          direction="ltr"
          custom-class="demo-drawer"
          ref="drawer"
        >
          <div class="demo-drawer__content">
            <el-form :model="form">
              <el-form-item label="演出厅名称" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="座位行数" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="座位列数" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="演出厅描述" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
            </el-form>
            <div class="demo-drawer__footer">
              <el-button @click="cancelForm">取 消</el-button>
              <el-button
                type="primary"
                @click="$refs.drawer.closeDrawer()"
                :loading="loading"
                >{{ loading ? "提交中 ..." : "确 定" }}</el-button
              >
            </div>
          </div>
        </el-drawer>
      </div>
      <el-table
        :data="
          tableData.filter(
            (data) =>
              !search || data.name.toLowerCase().includes(search.toLowerCase())
          )
        "
        style="width: 100%"
      >
        <el-table-column label="剧目名称" prop="name"> </el-table-column>
        <el-table-column label="演出厅" prop="studio"></el-table-column>
        <el-table-column label="是否安排验票" prop="isPlaned">
        </el-table-column>
        <el-table-column label="人员" prop="person"> </el-table-column>
        <el-table-column align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder="输入剧目名称搜索"
            />
          </template>
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"
              >Edit</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >Delete</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  name: "TicketInspection",
  data() {
    return {
      tableData: [
        {
          name: "蜘蛛侠",
          studio: "1号厅",
          isPlaned: "已安排",
          person: "李不要",
        },
        {
          name: "蜘蛛侠",
          studio: "1号厅",
          isPlaned: "已安排",
          person: "李不要",
        },
        {
          name: "蜘蛛侠",
          studio: "1号厅",
          isPlaned: "未安排",
          person: "",
        },
        {
          name: "蜘蛛侠",
          studio: "1号厅",
          isPlaned: "未安排",
          person: "",
        },
        {
          name: "蜘蛛侠",
          studio: "1号厅",
          isPlaned: "已安排",
          person: "李不要",
        },
        {
          name: "蜘蛛侠",
          studio: "1号厅",
          isPlaned: "已安排",
          person: "李不要",
        },
      ],
      search: "",
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
      formLabelWidth: "0px",
      timer: null,
      table: false,
      dialog: false,
      loading: false,
    };
  },
  methods: {
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    handleClose(done) {
      if (this.loading) {
        return;
      }
      this.$confirm("确定要提交表单吗？")
        .then((_) => {
          this.loading = true;
          this.timer = setTimeout(() => {
            done();
            // 动画关闭需要一定的时间
            setTimeout(() => {
              this.loading = false;
            }, 400);
          }, 2000);
        })
        .catch((_) => {});
    },
    cancelForm() {
      this.loading = false;
      this.dialog = false;
      clearTimeout(this.timer);
    },
  },
};
</script>
<style scoped>
.content {
  width: 80%;
  margin: 50px auto;
}
.el-table-column {
  text-align: center;
}
</style>