// 角色管理
<template>
  <div style="width: 80%; margin: 20px auto">
    <div class="search">
      <el-input v-model="username" placeholder="请输入角色名"></el-input>
      <el-button @click="search()">查询</el-button>
    </div>
    <!-- <el-button @click="clearFilter">取消筛选</el-button> -->
    <el-table ref="filterTable" :data="tableData" style="width: 100%">
      <el-table-column align="center" prop="picture" label="头像" width="100">
        <el-image
          style="
            width: 60px;
            height: 60px;
            border-radius: 50px;
            overflow: hidden;
          "
          src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"
          :preview-src-list="srcList"
        >
        </el-image>
      </el-table-column>
      <el-table-column
        align="center"
        prop="account"
        label="账号"
        width="180"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="sex"
        label="性别"
        width="180"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="tel"
        label="电话"
        width="180"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="permission"
        label="用户权限"
        width="210"
        :filters="[
          { text: '管理员', value: '管理员' },
          { text: '员工', value: '员工' },
        ]"
        :filter-method="filterPermission"
        filter-placement="bottom-end"
      >
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.permission === '管理员' ? 'primary' : 'success'"
            disable-transitions
            >{{ scope.row.permission }}</el-tag
          >
        </template>
      </el-table-column>
      <el-table-column align="center">
        <template slot="header" slot-scope=""> 权限管理 </template>
        <template slot-scope="scope">
          <el-button
            v-if="judgePermission(scope.$index)"
            size="mini"
            type="primary"
            @click="grantRole(scope.$index, scope.row)"
            >设为管理员</el-button
          >
          <el-button
            v-else
            size="mini"
            type="success"
            @click="grantRole(scope.$index, scope.row)"
            >设为员工</el-button
          >
        </template>
      </el-table-column>
      <!-- <el-table-column align="center">
        <template slot="header" slot-scope=""> 人员操作 </template>
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column> -->
    </el-table>
  </div>
</template>
<script>
export default {
  name: "RolesManagement",
  data() {
    return {
      tableData: [
        {
          account: "adm123",
          sex: "男",
          tel: "123343434",
          permission: "观众",
        },
        {
          account: "1543241232",
          tel: "123343434",
          sex: "女",
          permission: "管理员",
        },
        {
          account: "1312456123",
          tel: "123343434",
          sex: "男",
          permission: "售票员",
        },
        {
          account: "5131234123",
          tel: "123343434",
          sex: "女",
          permission: "会计",
        },
      ],
    };
  },
  async created() {
    const { data: res } = await this.$http.get();
    console.log(res);
  },
  methods: {
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
      let person = this.$data.userInfo[index];
      person.station = null;
      person.role = null;
      console.log(person);
      this.$http({
        method: "post",
        url: "/person/loginWeb/manage/delete",
        data: person,
      }).then((result) => {
        console.log(result);
        let res = result.data;
        this.requestUserInfo();
      });
    },
    // 修改用户权限
    grantRole(index, row) {
      console.log(index, row);
      let person = this.$data.userInfo[index];
      person.station = null;
      person.role = null;
      console.log(person);
      this.$http({
        method: "post",
        url: "/person/loginWeb/manage/grantRole",
        data: person,
      }).then((result) => {
        console.log(result);
        let res = result.data;
        this.requestUserInfo();
      });
    },
    // 判断用户权限
    judgePermission(index) {
      if (this.$data.tableData[index].permission == "管理员") {
        return false;
      } else {
        return true;
      }
    },
    // 筛选标签
    clearFilter() {
      console.log(this.$refs);
      this.$refs.filterTable.clearFilter();
    },
    formatter(row, column) {
      return row.address;
    },
    filterPermission(value, row) {
      return row.permission === value;
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    // 请求用户信息
    requestUserInfo() {
      this.$http({
        method: "get",
        url: "/person/loginWeb/manage/find",
      }).then((result) => {
        console.log(result);
        let res = result.data;
        this.$data.userInfo = res.data;
        // map 处理数据格式，后绑定至tableData。
        let tableData = res.data.map(function (elem) {
          let newData = {};
          if (elem.role == null) {
            newData.permission = "员工";
          } else {
            newData.permission = elem.role.admin;
          }
          newData.name = elem.name;
          newData.account = elem.userName;
          newData.nums = elem.nums;
          return newData;
        });
        this.$data.tableData = tableData;
      });
    },
  },
  mounted() {
    this.requestUserInfo();
  },
};
</script>
<style scoped>
.search {
  width: 80%;
  margin: 40px auto 10px;
}
.search .el-input {
  width: 200px;
  margin-right: 20px;
}
</style>