// 工作台
<template>
  <div>
    <div class="role">
      <el-tag class="l">身份</el-tag>
      <el-tag type="" effect="dark">{{ this.roles[type].role }}</el-tag>
    </div>
    <div class="power">
      <el-tag class="l">权限</el-tag>
      <el-tag type="success" effect="dark">{{ this.roles[type].power }}</el-tag>
    </div>
    <div class="turn" @click="turnTo()">
      <button>点此进入剧院后台管理系统</button>
    </div>
  </div>
</template>
<script>
export default {
  name: "WorkBenches",
  data() {
    return {
      type: 1,
      roles: [
        {
          role: "管理员",
          power: "管理员拥有该系统的所有权限",
        },
        {
          role: "售票员",
          power: "售票员可以对剧院进行票务管理",
        },
        {
          role: "财务部门",
          power: "财务部可以对剧院进行财务管理",
        },
        {
          role: "业务部门",
          power: "业务部门可以对剧院进行剧院管理",
        },
      ],
    };
  },
  methods: {
    turnTo() {
      this.$router.push("/Main");
    },
  },
};
</script>
<style scoped>
.role,
.power {
  height: 40px;
  line-height: 40px;
  margin-top: 20px;
}
.el-tag {
  width: 200px;
  height: 50px;
  line-height: 50px;
}
.l {
  width: 50px;
  /* margin-right: 20px; */
}
.turn {
  margin-top: 100px;
}
button {
  width: 300px;
  height: 50px;
  color: #0ebeff;
  font-size: 24px;
  background-color: #000;
  border: none;
  outline: none;
  z-index: 1;
  border-radius: 10px;
  /* outline:4px solid #fff; */
  position: relative;
  overflow: hidden;
}
button::before {
  content: "";
  position: absolute;
  /* background: rgb(238, 0, 255); */
  background-color: #fff;
  width: 200%;
  height: 200%;
  z-index: -2;
  left: 50%;
  top: 50%;
  transform-origin: 0 0;
  animation: rotate 3s infinite linear;
}
button::after {
  content: "";
  position: absolute;
  background-color: #000;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  left: 2px;
  top: 2px;
  border-radius: 10px;
  z-index: -1;
}
@keyframes rotate {
  to {
    transform: rotate(1turn);
  }
}
</style>