<template>
  <div style="width: 100%">
    <!-- 轮播图/走马灯 -->
    <el-main>
      <div class="carousel">
        <el-carousel :interval="3000" arrow="always">
          <el-carousel-item v-for="(item, index) in this.imgList" :key="index">
            <img :src="item" alt="图片" />
          </el-carousel-item>
        </el-carousel>
      </div>
      <!-- 电影展示 -->
      <div class="show">
