            :key="index"
          >
            <el-card shadow="always">
              <div class="img" @click="turnToMovieDetails(item)">
                <img
                  :src="item.imgUrl"
                  alt="电影"
                  @click="turnToMovieDetails(item)"
                />
                <div class="title">购票</div>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div></el-main
    >
    <el-footer>
      <div class="top">
        <ul>
          <li>关于我们&nbsp;|&nbsp;</li>
          <li>联系我们&nbsp;|&nbsp;</li>
          <li>联系客服&nbsp;|&nbsp;</li>
