                >西安市长安区西长安街618号东区计算机学院</a
              >
            </li>
            <li><i class="el-icon-phone"></i>029-88166712</li>
            <li><i class="el-icon-message"></i> shuxf@xiyou.edu.cn</li>
            <li>@ Copyright &copy; 2020. 西安邮电大学计算机学院.</li>
          </ul>
        </div>
        <div class="right">
          <div class="codes">扫码联系</div>
          <ul>
            <li>
              <div class="title" style="height: 50px">联系人1</div>
              <img src="../assets/img/code.png" alt="" />
            </li>
            <li>
              <div class="title" style="height: 50px">联系人2</div>
              <img src="../assets/img/code.png" alt="" />
            </li>
            <li>
              <div class="title" style="height: 50px">联系人3</div>
