</template>
<script>
export default {
  name: "HomeTo",
  data() {
    return {
      movieList: [],
      imgList: [
        require("@/assets/img/carousel1.jpg"),
        require("@/assets/img/carousel2.png"),
        require("@/assets/img/carousel3.jpg"),
        require("@/assets/img/carousel4.png"),
      ],
    };
  },
  async created() {
    const { data: data } = await this.$http.get(`/video/getVideo`, {
      params: {
        // 1页个数
        pageByTotalNum: 8,
        // 第几页
        page: 1,
      },
    });
    this.movieList = data.value.object;
  },
  methods: {
    turnToMovieDetails(item) {
      window.localStorage.setItem("movieDetails", JSON.stringify(item));
      this.$router.push("/MovieDetails");
    },
  },
};
</script>
