// 座位选择
<template>
  <div style="width: 100%">
    <div class="content">
      <div class="top"></div>
      <div class="main">
        <div class="left">
          <div class="seatIntroduce">
            <div class="seat1">
              <span><img src="../assets/img/seat1.png" alt="" /></span>
              <span>可选座位</span>
            </div>
            <div class="seat2">
              <span><img src="../assets/img/seat2.png" alt="" /></span>
              <span>已选座位</span>
            </div>
            <div class="seat3">
              <span><img src="../assets/img/seat3.png" alt="" /></span>
              <span>已售座位</span>
            </div>
            <div class="clear"></div>
          </div>

          <!-- 座位展示 -->
          <div class="show">
            <!-- 屏幕 -->
            <div class="screen">
              <div class="img">
                <img src="../assets/img/screen.png" alt="银幕" />
              </div>
              <!-- <div class="text">银幕中央</div> -->
            </div>
            <!-- 座位 -->
            <div class="seat">
              <!-- 左侧列表 -->
              <div class="left">
                <div
                  class="list"
                  v-for="index of this.studioData.rows"
                  :key="index"
                >
                  <div style="height: 26px; line-height: 26px">
                    {{ index }}
                  </div>
                </div>
              </div>
              <!-- 图片 -->
              <div
                class="row"
                v-for="(item, index) in this.seatInfo"
                :key="index"
              >
                <div class="r" v-for="(items, indexs) in item" :key="indexs">
                  <div
                    class="co unchoosed"
                    v-if="items.status == unsoldStatus"
                    @click="chooseSeat(items)"
                  >
                    <img src="../assets/img/seat1.png" alt="" />
                  </div>
                  <div
                    class="co choosed"
                    v-if="items.status == choosedStatus"
                    @click="restoreSeat(items)"
                  >
                    <img src="../assets/img/seat2.png" alt="" />
                  </div>
                  <div
                    class="co notChoosed"
                    v-if="
                      items.status == soldStatus ||
                      items.status == lockingStatus
                    "
                  >
                    <img src="../assets/img/seat2.png" alt="" />
                  </div>
                </div>
              </div>
              <div class="clear"></div>
            </div>
          </div>
        </div>
        <!-- 右侧 -->
        <div class="right">
          <div class="top">
            <div class="t">
              <div class="img">
                <img :src="this.filmInfo.imgUrl" alt="电影" />
              </div>
              <div class="description">
                <div class="name">{{ this.plan.video }}</div>
                <div class="type">类型：{{ this.filmInfo.type }}</div>
                <div class="duration">时长：{{ this.filmInfo.duration }}</div>
              </div>
              <div class="clear"></div>
            </div>
            <div class="next">
              <div class="cinema">影院：{{ this.cinema }}</div>
              <div class="studio">影厅：{{ this.plan.studio }}</div>
              <!-- <div class="version">版本：{{ this.filmInfo.version }}</div> -->
              <div class="session">
                场次：<span>今天 &nbsp; {{ this.filmInfo.releaseTime }}</span>
              </div>
              <div class="price">票价：￥{{ this.plan.price }}/张</div>
            </div>
          </div>
          <div class="middle">
            <div class="t">座位：一次最多选择6个座位</div>
            <div class="choosedSeatShow">
              <span v-for="(item, index) in this.choosedSeat" :key="index"
                >{{ item.ticketRows }}行{{ item.ticketColumns }}列</span
              >
            </div>
            <div class="midd">请<span>选择左侧</span>座位图选择座位</div>
            <div class="totalPrice">
              <span>总价：&nbsp;￥&nbsp;</span
              ><span style="color: red; font-size: 20px">
                {{ this.totalPrice }}
              </span>
            </div>
          </div>
          <div class="bottom">
            <!-- <div class="accounter">账号：{{ this.filmInfo.accounter }}</div> -->
            <div class="confirm" @click="turnToPayMent()">确认选座</div>
          </div>
        </div>
        <div class="clear"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "SeatSelection",
  data() {
    return {
      cinema: "天幕影院",
      // 未售出状态
      unsoldStatus: "Unsold",
      choosedStatus: "choosed",
      soldStatus: "Sold",
      lockingStatus: "Locking",
      plan: {},
      filmInfo: {},
      // 选择的票数
      choosedNumber: 0,
      // 选择的座位信息
      choosedSeat: [],
      // 总价
      totalPrice: 0,
      // 座位信息
      seatInfo: [],
      studioData: {
        rows: 8,
        columns: 8,
      },
    };
  },
  async created() {
    let nowTime = new Date();
    this.month = nowTime.getMonth() + 1;
    this.day = nowTime.getDate();
    // 演出计划
    this.plan = JSON.parse(window.localStorage.getItem("planData"));
    let videoTemp = this.plan.video;
    console.log(this.plan);
    console.log(videoTemp);
    const { data: res } = await this.$http.get("/video/getVideoByName", {
      params: {
        pageByTotalNum: "6",
        page: "1",
        name: videoTemp,
      },
    });
    console.log(res);
    this.filmInfo = res.value.object[0];
    // 将时间戳转化为月、日、时分秒
    let temp1 = "";
    temp1 += new Date(this.filmInfo.releaseTime).getMonth() + 1 + "月";
    temp1 += new Date(this.filmInfo.releaseTime).getDate() + "日";
    temp1 +=
      new Date(this.filmInfo.releaseTime).getHours() > 10
        ? new Date(this.filmInfo.releaseTime).getHours() + ":"
        : "0" + new Date(this.filmInfo.releaseTime).getHours() + ":";
    temp1 +=
      new Date(this.filmInfo.releaseTime).getMinutes() > 10
        ? new Date(this.filmInfo.releaseTime).getMinutes() + ":"
        : "0" + new Date(this.filmInfo.releaseTime).getMinutes() + ":";
    temp1 +=
      new Date(this.filmInfo.releaseTime).getSeconds() > 10
        ? new Date(this.filmInfo.releaseTime).getSeconds()
        : "0" + new Date(this.filmInfo.releaseTime).getSeconds();
    this.filmInfo.releaseTime = temp1;
    // console.log(this.filmInfo);
    // 根据演出计划id获取票
    const { data: rest } = await this.$http.get("/ticket/getTickerByPlanId", {
      params: {
        id: this.plan.id,
      },
    });
    console.log(rest);
    this.studioData.rows = rest.value.rows;
    this.studioData.columns = rest.value.columns;
    let tempArr = rest.value.tickets;
    for (let i = 0; i < tempArr.length; i += this.studioData.columns) {
      const arr1 = tempArr.slice(i, i + this.studioData.columns);
      this.seatInfo.push(arr1);
    }
  },
  methods: {
    chooseSeat(items) {
      items.status = "choosed";
      this.choosedNumber++;
      this.choosedSeat.push({
        ticketRows: items.ticketColumns,
        ticketColumns: items.ticketRows,
        id: items.id,
        planId: items.planId,
      });
      this.totalPrice = this.plan.price * this.choosedNumber;
    },
    restoreSeat(items) {
      items.status = "Unsold";
      for (let i = 0; i < this.choosedSeat.length; i++) {
        if (
          this.choosedSeat[i].ticketRows === items.ticketColumns &&
          this.choosedSeat[i].ticketColumns === items.ticketRows
        ) {
          this.choosedSeat.splice(i, 1);
          this.choosedNumber--;
        }
      }
      this.totalPrice = this.plan.price * this.choosedNumber;
    },
    async turnToPayMent() {
      const { data: res } = await this.$http.post("/ticket/updateTicket/buy", {
        token: window.localStorage.getItem("token"),
        value: {
          tickets: this.choosedSeat,
        },
        uid: 1,
      });
      console.log(res.value);
      window.localStorage.setItem("orderInfo", res.value);
      this.$router.push("/PayMent");
    },
  },
};
</script>
