// 票务管理
<template>
  <div>
    <div class="search">
      <el-form :inline="true" label-position="left">
        <el-form-item label="剧目名称:" prop="searchName">
          <el-input v-model="searchName"></el-input>
        </el-form-item>
        <el-button @click="search()">查询</el-button>
        <div class="clear"></div>
      </el-form>
    </div>
    <div class="show">
      <div
        class="content"
        v-for="(item, index) in this.movieList"
        :key="index"
        @click="turnToMovieDetails(item)"
      >
        <div class="img">
          <img :src="item.imgUrl" alt="电影图片" />
        </div>
        <div class="name">{{ item.name }}</div>
        <div class="score" style="color: #ff8400">
          <i class="integer" style="font-size: 56px">{{
            parseInt(item.score) + "."
          }}</i>
          <i class="fraction" style="font-size: 26px">{{
            ("" + (item.score % 1))[2]
          }}</i>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import LoginVue from "./Login.vue";
export default {
  name: "TicketManagement",
  data() {
    return {
      searchName: "",
      movieList: [],
    };
  },
  async created() {
    const { data: res } = await this.$http.get("/video/getVideoByHot", {
      params: {
        pageByTotalNum: 8,
        page: 1,
      },
    });
    console.log(res);
    this.movieList = res.value.object;
  },
  methods: {
    async search() {
      const { data: res } = await this.$http.get("/video/getVideoByName", {
        params: {
          pageByTotalNum: "6",
          page: "1",
          name: this.searchName,
        },
      });
      this.movieList = res.value.object;
    },
    turnToMovieDetails(item) {
      window.localStorage.setItem("movieDetails", JSON.stringify(item));
      this.$router.push("/MovieDetails");
    },
  },
};
</script>
<style scoped>
.search {
  width: 80%;
  margin: 40px auto 0;
}
.search .el-input {
  width: 200px;
  margin-right: 20px;
}
/* 展示 */
.show {
  width: 680px;
  margin: 10px auto;
}
.content {
  width: 160px;
  float: left;
  margin-right: 10px;
}
.content img {
  width: 160px;
  height: 220px;
}
.content .img {
  width: 160px;
}
.content .name {
  width: 160px;
  font-size: 16px;
  color: #333333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.content .score {
  width: 160px;
}
</style>