// 售票员的销售统计
<template>
  <div>
    <div class="top">
      <div class="time">
        <span>{{ this.todayDate }}</span>
        &nbsp;&nbsp;&nbsp;
        <span style="color: green">已更新</span>
      </div>
    </div>
    <div class="salesShow">
      <ul>
        <li>
          <el-tag class="l">销售额：</el-tag
          ><el-tag effect="dark">{{ this.salesData.salesVolume }}</el-tag>
        </li>
        <li>
          <el-tag class="l">票数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.ticketNumber }}</el-tag>
        </li>
        <li>
          <el-tag class="l">退票数：</el-tag
          ><el-tag effect="dark">{{ this.salesData.refundNumber }}</el-tag>
        </li>
        <li>
          <el-tag class="l">折扣：</el-tag
          ><el-tag effect="dark">{{ this.salesData.discount }}</el-tag>
        </li>
        <li>
          <el-tag class="l">客户数量：</el-tag
          ><el-tag effect="dark">{{ this.salesData.customerNumber }}</el-tag>
        </li>
        <li>
          <el-tag class="l">支付方式：</el-tag
          ><el-tag effect="dark">{{ this.salesData.payment }}</el-tag>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "SalesStatisticsOne",
  data() {
    return {
      todayDate: "",
      salesData: {
        salesVolume: "1232",
        ticketNumber: "12",
        refundNumber: "1",
        discount: "12",
        customerNumber: "7",
        payment: "微信支付",
      },
    };
  },
  created() {
    var temp = new Date();
    this.todayDate += temp.getFullYear() + "-";
    this.todayDate += temp.getMonth() + 1;
    this.todayDate += "-" + temp.getDate();
  },
  methods: {},
};
</script>
<style scoped>
.top {
  margin-top: 20px;
}
.top .time {
  font-size: 20px;
}
.salesShow {
  width: 400px;
  margin: 10px auto;
}
.salesShow ul li {
  margin-bottom: 10px;
  font-size: 16px;
}
.el-tag {
  width: 250px;
  height: 60px;
  line-height: 60px;
  border: 0;
  border-radius: 0;
  font-size: 16px;
}
.l {
  width: 100px;
  margin-right: 1px;
}
</style>