// 已完成
<template>
  <div class="index">
    <h2>演出厅管理</h2>
    <!-- 查询 -->
    <div class="search">
      <div class="inp">
        <el-form :inline="true" label-position="left">
          <el-form-item label="演出厅名称:" prop="searchName">
            <el-input v-model="searchName"></el-input>
          </el-form-item>
          <el-button @click="search()">查询</el-button>
          <el-button @click="insert()">增加</el-button>
          <div class="clear"></div>
        </el-form>
      </div>
    </div>
    <!-- 展示 -->
    <div class="show">
      <h3>演出厅展示</h3>
      <table>
        <thead>
          <tr>
            <th>演出厅编号</th>
            <th>演出厅名称</th>
            <th>座位行数</th>
            <th>座位列数</th>
            <th>演出厅描述</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in this.studioDate" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.rows }}</td>
            <td>{{ item.columns }}</td>
            <td>{{ item.description }}</td>
            <td>
              <button @click="editStudio(item)">修改</button>
              <button @click="deleteStudio(item)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- 添加 -->
    <div class="addForm">
      <el-form
        v-show="addStudio"
        :model="selectedStudio"
        status-icon
        label-width="100px"
      >
        <h3 class="Title">添加演出厅信息</h3>
        <el-form-item label="演出厅名称" prop="selectedStudio.name">
          <el-input v-model="selectedStudio.name"></el-input>
        </el-form-item>
        <el-form-item label="演出厅行数" prop="selectedStudio.rows">
          <el-input v-model="selectedStudio.rows"></el-input>
        </el-form-item>
        <el-form-item label="演出厅列数" prop="selectedStudio.columns">
          <el-input v-model="selectedStudio.columns"></el-input>
        </el-form-item>
        <el-form-item label="演出厅描述" prop="selectedStudio.description">
          <el-input v-model="selectedStudio.description"></el-input>
        </el-form-item>
        <el-button type="primary" @click="submitAdd()">提交添加</el-button>
        <el-button type="primary" @click="cancleAdd()">取消</el-button>
      </el-form>
    </div>
    <!-- 修改 -->
    <div class="updateForm">
      <el-form
        v-show="updateStudio"
        :model="selectedStudio"
        status-icon
        label-width="100px"
      >
        <h3 class="Title">修改演出厅信息</h3>
        <el-form-item label="演出厅id" prop="selectedStudio.id">
          <el-input v-model="selectedStudio.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="演出厅名称" prop="selectedStudio.name">
          <el-input v-model="selectedStudio.name"></el-input>
        </el-form-item>
        <el-form-item label="演出厅行数" prop="selectedStudio.rows">
          <el-input v-model="selectedStudio.rows" disabled></el-input>
        </el-form-item>
        <el-form-item label="演出厅列数" prop="selectedStudio.columns">
          <el-input v-model="selectedStudio.columns" disabled></el-input>
        </el-form-item>
        <el-form-item label="演出厅描述" prop="selectedStudio.description">
          <el-input v-model="selectedStudio.description"></el-input>
        </el-form-item>
        <el-button type="primary" @click="saveStudio()">提交修改</el-button>
        <el-button type="primary" @click="cancleSave()">取消</el-button>
      </el-form>
    </div>
    <div class="clear"></div>
  </div>
</template>
<script>
export default {
  name: "StudioManagement",
  data() {
    return {
      searchName: "",
      studioDate: [],
      updateStudio: false,
      addStudio: false,
      selectedStudio: {
        id: "",
        name: "",
        rows: "",
        columns: "",
        description: "",
      },
    };
  },
  // 获取演出厅数据
  async created() {
    console.log(window.localStorage.getItem("token"));
    const { data: res } = await this.$http.get("/Studio/getStudio", {
      params: {
        pageSize: "10",
        page: "1",
        token: window.localStorage.getItem("token"),
      },
    });
    this.studioDate = res.value.object;
  },
  methods: {
    // 查询
    async search() {
      const { data: res } = await this.$http.get("/Studio/getStudioByName", {
        params: {
          pageSize: "10",
          page: "1",
          token: window.localStorage.getItem("token"),
          name: this.searchName,
        },
      });
      this.studioDate = res.value.object;
    },
    // 显示添加表单
    async insert() {
      this.addStudio = true;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "airblue";
      a.style.border = "1px solid blueviolet";
      a.style.boxShadow = "0 0 25px #f885ff";
    },
    // 提交添加修改
    async submitAdd() {
      // 请求
      const { data: res } = await this.$http.post(`/Studio/insertStudio`, {
        token: window.localStorage.getItem("token"),
        value: {
          name: this.selectedStudio.name,
          rows: this.selectedStudio.rows,
          columns: this.selectedStudio.columns,
          description: this.selectedStudio.description,
        },
      });
      console.log(res);
      // 插入后知更新
      const { data: rest } = await this.$http.get("/Studio/getStudio", {
        params: {
          pageSize: "10",
          page: "1",
          token: window.localStorage.getItem("token"),
        },
      });
      console.log(rest);
      this.studioDate = rest.value.object;
      this.addStudio = false;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    cancleAdd() {
      this.addStudio = false;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    // 更新表单可见
    async editStudio(item) {
      this.updateStudio = true;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "airblue";
      a.style.border = "1px solid blueviolet";
      a.style.boxShadow = "0 0 25px #f885ff";
      this.selectedStudio = item;
    },
    //提交更新表单
    async saveStudio() {
      const { data: res } = await this.$http.post(`/Studio/updateStudio`, {
        token: window.localStorage.getItem("token"),
        value: {
          id: this.selectedStudio.id,
          name: this.selectedStudio.name,
          rows: this.selectedStudio.rows,
          columns: this.selectedStudio.columns,
          description: this.selectedStudio.description,
        },
      });
      const { data: rest } = await this.$http.get("/Studio/getStudio", {
        params: {
          pageSize: "10",
          page: "1",
          token: window.localStorage.getItem("token"),
        },
      });
      this.studioDate = rest.value.object;
      this.updateStudio = false;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    async deleteStudio(item) {
      this.selectedStudio = item;
      const { data: res } = await this.$http.get("/Studio/deleteStudio", {
        params: {
          id: this.selectedStudio.id,
          token: window.localStorage.getItem("token"),
        },
      });
      const { data: rest } = await this.$http.get("/Studio/getStudio", {
        params: {
          pageSize: "10",
          page: "1",
          token: window.localStorage.getItem("token"),
        },
      });
      this.studioDate = rest.value.object;
    },
    cancleSave() {
      this.updateStudio = false;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
  },
};
</script>
<style scoped>
.index {
  padding: 20px;
  height: 100%;
}
h2 {
  text-align: center;
}
.search .el-form-item {
  float: left;
  width: 200px;
}
.search .el-button {
  float: left;
  width: 60px;
  text-align: center;
  border-radius: 5px;
  margin-top: 40px;
}
.show {
  width: 60%;
  margin: 0 auto;
}
.show table tr {
  width: 100%;
}
.show table th,
.show table td {
  text-align: center;
  width: 100px;
  height: 40px;
}
.show table td button {
  width: 48px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  margin-right: 2px;
}
/* 添加表单 */
.addForm {
  float: left;
  width: 350px;
  height: 400px;
  margin: 10px 100px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
}
/* 修改表单 */
.updateForm {
  float: right;
  width: 350px;
  height: 400px;
  margin: 10px 100px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
}
.el-button {
  float: left;
  width: 100px;
  text-align: center;
  border-radius: 5px;
  margin: 10px 30px 0 0;
}
</style>