// 完成
<template>
  <div>
    <div class="header">
      <div class="img">
        <img src="../assets/img/tianmu.jpg" alt="天幕剧院" />
      </div>
      <div class="loginTo" @click="loginTo()">去登录</div>
      <div class="login">已有账号 &nbsp;| &nbsp;</div>
      <div class="clear"></div>
    </div>
    <el-form
      :model="registerForm"
      status-icon
      :rules="registerRules"
      ref="registerFormRef"
      label-width="100px"
      class="registerContainer"
    >
      <h3 class="registerTitle">天幕剧院</h3>
      <el-form-item label="账号" prop="username">
        <el-input v-model.number="registerForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="registerForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-input
          type="password"
          v-model="registerForm.checkPass"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-button type="primary" style="width: 100%" @click="register()"
        >点击注册</el-button
      >
    </el-form>
  </div>
</template>
<script>
export default {
  name: "RegisterTo",
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.registerForm.password) {
        callback(new Error("两次输入密码不一致！"));
      } else {
        callback();
      }
    };
    return {
      confirmPwd: "",
      registerForm: {
        username: "",
        password: "",
        checkPass: "",
      },
      registerRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "用户名长度在1 到 10 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "密码长度在 6 到 15 个字符",
            trigger: "blur",
          },
        ],
        checkPass: [
          { required: true, message: "请再次输入确认密码", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "密码长度在 6 到 15 个字符",
            trigger: "blur",
          },
          {
            validator: validatePass,
            trigger: "blur",
          },
        ],
      },
      //
    };
  },
  methods: {
    loginTo() {
      this.$router.push("/Login");
    },
    register() {
      this.$refs.registerFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(`/user/register`, {
          username: this.registerForm.username,
          password: this.registerForm.password,
        });
        if (res.code !== 0) {
          return this.$message({
            type: "error",
            message: res.message,
            duration: 2500,
          });
        }
        this.$message.success("注册成功，即将返回登录页面!");
        this.$router.push("/login");
      });
    },
  },
};
</script>
<style scoped>
/* 头部 */
.header {
  width: 100%;
  height: 100px;
  border-bottom: 1px solid gray;
}
.header .img {
  float: left;
  margin: 20px 100px;
}
.header .img img {
  width: 200px;
  height: 60px;
}
.login {
  float: right;
  height: 100px;
  line-height: 150px;
}
.loginTo {
  float: right;
  color: red;
  line-height: 150px;
  margin-right: 100px;
}
.loginTo:hover {
  text-decoration: underline;
}
/* 表单 */
.registerContainer {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 100px auto;
  width: 350px;
  padding: 15px 35px 15px 35px;
  background: aliceblue;
  border: 1px solid blueviolet;
  box-shadow: 0 0 25px #f885ff;
  box-sizing: border-box;
}
.registerTitle {
  margin: 0px auto 48px auto;
  text-align: center;
  font-size: 40px;
}
</style>