import Vue from 'vue'
import VueRouter from 'vue-router'
import First from '../components/First.vue'
import Home from '../components/Home.vue'
import HotScreening from '../components/HotScreening.vue'
import HistoricalRankings from '../components/HistoricalRankings'
import AboutUs from '../components/AboutUs.vue'
import Login from '../components/Login.vue'
import Register from '../components/Register.vue'
import Main from '../components/Main.vue'
import StudioManagement from '../components/StudioManagement.vue'
import PlayManagement from '../components/PlayManagement.vue'
import PerformancePlan from '../components/PerformancePlan.vue'
import TicketInspection from '../components/TicketInspection.vue'
import AudienceManagement from '../components/AudienceManagement.vue'
import StaffManagement from '../components/StaffManagement.vue'
import TicketManagement from '../components/TicketManagement.vue'
import RefundManagement from '../components/RefundManagement.vue'
import SalesStatisticsOne from '../components/SalesStatisticsOne.vue'
import TicketMoneyManagement from '../components/TicketMoneyManagement.vue'
import SalesPerformance from '../components/SalesPerformance.vue'
import SalesStatisticsTwo from '../components/SalesStatisticsTwo.vue'
import ResourceManagement from '../components/ResourceManagement.vue'
import RolesManagement from '../components/RolesManagement.vue'
import MovieDetails from '../components/MovieDetails.vue'
import SeatSelection from '../components/SeatSelection.vue'
import PersonalCenter from '../components/PersonalCenter.vue'
import MyOrder from '../components/MyOrder.vue'
import BasicInformation from '../components/BasicInformation.vue'
import WorkBenches from '../components/WorkBenches.vue'
import PayMent from '../components/PayMent.vue'
Vue.use(VueRouter)

//解决重复点击导航出现的bug
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    name: 'First',
    component: First,
    children: [
      {
        path: "/Home",
        name: "Home",
        component: Home
      }, {
        path: '/HotScreening',
        name: 'HotScreening',
        component: HotScreening
      },
      {
        path: '/HistoricalRankings',
        name: "HistoricalRankings",
        component: HistoricalRankings
      }, {
        path: '/AboutUs',
        name: 'AboutUs',
        component: AboutUs
      }, {
        path: "/MovieDetails",
        component: MovieDetails
      }, {
        path: "/SeatSelection",
        component: SeatSelection
      },
      {
        path: "/PayMent",
        component: PayMent,
      }, {
        path: "/PersonalCenter",
        component: PersonalCenter,
        children: [{
          path: "/MyOrder",
          component: MyOrder
        }, {
          path: "/BasicInformation",
          component: BasicInformation
        }, {
          path: "/WorkBenches",
          component: WorkBenches
        }]
      }]
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  }, {
    path: '/Register',
    name: 'register',
    component: Register
  },
  {
    path: '/main',
    name: 'main',
    component: Main,
    children: [{
      path: "/StudioManagement",
      component: StudioManagement
    }, {
      path: "/PlayManagement",
      component: PlayManagement
    }, {
      path: "/PerformancePlan",
      component: PerformancePlan
    }, {
      path: "/TicketInspection",
      component: TicketInspection
    }, {
      path: "/AudienceManagement",
      component: AudienceManagement
    }, {
      path: "/StaffManagement",
      component: StaffManagement
    }, {
      path: "/TicketManagement",
      component: TicketManagement
    }, {
      path: "/RefundManagement",
      component: RefundManagement
    }, {
      path: "/SalesStatisticsOne",
      component: SalesStatisticsOne
    }, {
      path: "/TicketMoneyManagement",
      component: TicketMoneyManagement
    }, {
      path: "/SalesPerformance",
      component: SalesPerformance
    }, {
      path: "/SalesStatisticsTwo",
      component: SalesStatisticsTwo
    }, {
      path: "/ResourceManagement",
      component: ResourceManagement
    }, {
      path: "/RolesManagement",
      component: RolesManagement
    }]
  },
]

const router = new VueRouter({
  routes
})

export default router
