// 完成
<template>
  <div style="width: 100%">
    <div class="top">正在热映({{ this.hotScreeningNumber }}部)</div>
    <div class="main">
      <div
        class="content"
        v-for="(item, index) in this.movieList"
        :key="index"
        @click="turnToMovieDetails(item)"
      >
        <div class="img">
          <img :src="item.imgUrl" alt="电影图片" />
        </div>
        <div class="description">{{ item.description }}</div>
        <div class="score" style="color: #ff8400">
          <i class="integer" style="font-size: 56px">{{
            parseInt(item.score) + "."
          }}</i>
          <i class="fraction" style="font-size: 26px">{{
            ("" + (item.score % 1))[2]
          }}</i>
        </div>
      </div>
      <div class="clear"></div>
    </div>
    <div class="top2">即将上映({{ this.upComingNumber }}部)</div>
    <div class="main2">
      <div
        class="content"
        v-for="(item, index) in this.movieList2"
        :key="index"
        @click="turnToMovieDetails(item)"
      >
        <div class="img">
          <img :src="item.imgUrl" alt="电影图片" />
        </div>
        <div class="description">{{ item.description }}</div>
        <div class="score" style="color: #ff8400">
          <i class="integer" style="font-size: 56px">{{
            parseInt(item.score) + "."
          }}</i>
          <i class="fraction" style="font-size: 26px">{{
            ("" + (item.score % 1))[2]
          }}</i>
        </div>
      </div>
      <div class="clear"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "HotScreening",
  data() {
    return {
      hotScreeningNumber: "48",
      upComingNumber: "12",
      movieList: [],
      movieList2: [],
    };
  },
  async created() {
    const { data: res } = await this.$http.get("/video/getVideoByHot", {
      params: {
        pageByTotalNum: 8,
        page: 1,
      },
    });
    this.movieList = res.value.object;
    const { data: rest } = await this.$http.get("/video/getVideoByComingSoon", {
      params: {
        pageByTotalNum: 8,
        page: 1,
      },
    });
    this.movieList2 = rest.value.object;
    console.log(this.movieList);
  },
  methods: {
    turnToMovieDetails(item) {
      window.localStorage.setItem("movieDetails", JSON.stringify(item));
      this.$router.push("/MovieDetails");
    },
  },
};
</script>
<style scoped>
.top,
.top2 {
  width: 80%;
  margin: 20px auto;
  font-size: 25px;
  text-align: left;
  color: red;
}
.main,
.main2 {
  width: 80%;
  margin: 0 auto;
}
.main .content,
.main2 .content {
  width: 160px;
  float: left;
  margin-right: 10px;
}
.content img {
  width: 160px;
  height: 220px;
}
.content .img {
  width: 160px;
}
.content .description {
  width: 160px;
  font-size: 16px;
  color: #333333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.content .score {
  width: 160px;
}
</style>