// 未完成
<template>
  <div>
    <div class="search">
      <el-input v-model="username" placeholder="请输入用户名"></el-input>
      <el-button @click="search()">查询</el-button>
    </div>
    <div class="main">
      <el-row :gutter="12">
        <el-col
          :span="8"
          v-for="(item, index) in this.audienceList"
          :key="index"
        >
          <el-card shadow="always">
            <div class="img">
              <img src="../assets/img/head_sculpture.jpg" alt="头像" />
            </div>
            <div class="username">
              <el-tag class="l">昵称：</el-tag
              ><el-tag effect="dark">{{ item.username }}</el-tag>
            </div>
            <div class="tel">
              <el-tag class="l">手机号:</el-tag>
              <el-tag effect="dark">{{ item.phone }}</el-tag>
            </div>
            <div class="sex">
              <el-tag class="l">性别：</el-tag
              ><el-tag effect="dark">{{ item.sex }}</el-tag>
            </div>
            <!-- <el-button class="reset">重置该用户的密码</el-button> -->
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: "AudienceManagement",
  data() {
    return {
      username: "",
      audienceList: [],
    };
  },
  async created() {
    // 获取所有的观众
    const { data: res } = await this.$http.get("/user/usersInfo", {
      params: {
        token: window.localStorage.getItem("token"),
        isAdmin: false,
      },
    });
    this.audienceList = res.value;
  },
  methods: {
    // 根据观众usernsme查询
    async search() {
      const { data: res } = await this.$http.get("/user/getUserByName", {
        params: {
          token: window.localStorage.getItem("token"),
          name: this.username,
        },
      });
      this.audienceList = res.value;
    },
  },
};
</script>
<style scoped>
.search {
  width: 80%;
  margin: 40px auto 0;
}
.search .el-input {
  width: 200px;
  margin-right: 20px;
}
.main {
  width: 80%;
  margin: 20px auto;
}
.img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 0 auto;
}
.img img {
  width: 50px;
  height: 50px;
}
.el-card {
  position: relative;
  margin-bottom: 10px;
  height: 320px;
}
.tel,
.username,
.sex {
  height: 40px;
  margin-top: 20px;
  box-sizing: border-box;
}
.el-tag {
  width: 150px;
  height: 40px;
  line-height: 40px;
  border: 0;
  border-radius: 0;
}
.l {
  width: 50px;
  margin-right: 1px;
}
.reset {
  position: absolute;
  width: 150px;
  height: 40px;
  bottom: 10px;
  right: 10px;
}
</style>