// 完成
<template>
  <div class="index">
    <el-container>
      <el-header height="80px">
        <div class="img">
          <img src="../assets/img/tianmu.jpg" alt="天幕剧院" />
        </div>
        <div class="nav">
          <ul>
            <li @click="turnToHome()">首页</li>
            <li @click="turnToHotScreening()">正在热映</li>
            <li @click="turnToHistoricalRankings()">历史榜单</li>
            <li @click="turnToAboutUs()">关于我们</li>
            <li class="clear"></li>
          </ul>
        </div>
        <div class="notLogged" v-show="notLogged">
          <div class="login" @click="turnToRegister()">注册</div>
          <div class="btw">&nbsp;&nbsp;|&nbsp;&nbsp;</div>
          <div class="register" @click="turnToLogin()">登录</div>
          <div class="clear"></div>
        </div>
        <div class="logged" v-show="!this.notLogged">
          <div class="quit" @click="changeState()">
            <el-button type="danger" round>退出</el-button>
          </div>
          <div class="name" @click="turnToPersonalCenter()">
            <div class="up">{{ this.name }}</div>
            <div class="down">个人中心</div>
          </div>
          <div class="head_sculpture">
            <img src="../assets/img/head_sculpture.jpg" alt="" />
          </div>
        </div>
      </el-header>
      <el-container><router-view></router-view></el-container>
    </el-container>
  </div>
</template>
<script>
export default {
  name: "HomePage",
  data() {
    return {
      notLogged: true,
      name: "admin",
    };
  },
  created() {
    // window.localStorage.setItem("token", null);
    // console.log(window.localStorage.getItem("token"));
    let token = JSON.stringify(window.localStorage.getItem("token"));
    console.log(token);
    if (token === JSON.stringify("not")) {
      console.log(true);
      this.notLogged = true;
    } else {
      console.log(false);
      this.notLogged = false;
    }
  },
  methods: {
    turnToHome() {
      this.$router.push("/Home");
    },
    turnToHotScreening() {
      this.$router.push("/HotScreening");
    },
    turnToHistoricalRankings() {
      this.$router.push("/HistoricalRankings");
    },
    turnToAboutUs() {
      this.$router.push("/AboutUs");
    },
    turnToLogin() {
      this.$router.push("/Login");
    },
    turnToRegister() {
      this.$router.push("/Register");
    },
    turnToPersonalCenter() {
      this.$router.push("/PersonalCenter");
    },
    changeState() {
      window.localStorage.setItem("token", "not");
      this.notLogged = !this.notLogged;
    },
  },
};
</script>
<style scoped>
/* 头部 */
.el-header {
  height: 80px;
  color: #333;
  text-align: center;
  line-height: 80px;
  padding: 0;
}
/* 图片 */
.el-header .img {
  float: left;
  width: 150px;
  height: 80px;
  margin-left: 60px;
}
.el-header .img img {
  width: 150px;
  height: 70px;
  margin: 5px 0;
}
/* 导航栏 */
.el-header .nav {
  float: left;
  width: 60%;
  height: 100%;
}
.nav ul li {
  text-align: center;
  float: left;
  padding: 0 75px;
}
.nav ul li:hover {
  color: #fff;
  background-color: red;
}
/* 登录注册 */
.notLogged {
  float: right;
  margin-right: 20px;
}
.login {
  float: right;
}
.btw {
  float: right;
}
.register {
  float: right;
}
.login:hover,
.register:hover {
  color: red;
  text-decoration: underline;
}
/* 登录状态 */
.logged {
  float: right;
  margin-right: 20px;
}
.head_sculpture {
  float: right;
  margin: 15px 0;
  box-sizing: border-box;
}
.head_sculpture img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.name {
  float: right;
  width: 80px;
  margin-right: 50px;
  font-size: 14px;
}
.name .up,
.name .down {
  height: 40px;
}
.name .up {
  line-height: 450%;
}
.name .down {
  font-size: 13px;
  line-height: 200%;
}
.name:hover {
  color: red;
  text-decoration: underline;
}
/* 退出 */
.quit {
  float: right;
}
</style>