// 完成
<template>
  <div>
    <!-- 头部 -->
    <div class="header">
      <!-- 左侧灯笼 -->
      <div class="container1">
        <span class="head-line"></span>
        <div class="center">
          <div class="center-line">
            <span>欢迎登录</span>
          </div>
          <span class="footer-line"></span>
        </div>
      </div>
      <!-- 右侧灯笼 -->
      <div class="container2">
        <span class="head-line"></span>
        <div class="center">
          <div class="center-line">
            <span>欢迎登录</span>
          </div>
          <span class="footer-line"></span>
        </div>
      </div>
    </div>
    <!-- 表单 -->
    <el-form
      :model="loginForm"
      :rules="loginFormRules"
      ref="loginFormRef"
      class="loginContainer"
    >
      <h3 class="loginTitle" @click="turnToPersonalCenter()">天幕剧院</h3>
      <el-form-item prop="username">
        <el-input
          type="text"
          v-model="loginForm.username"
          placeholder="请输入账号"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          type="password"
          v-model="loginForm.password"
          placeholder="请输入密码"
        >
        </el-input>
      </el-form-item>
      <el-button type="primary" style="width: 100%" @click="submitLogin()"
        >登录</el-button
      >
      <div class="register" @click="turnToRegister()">还没有账号，前往注册</div>
    </el-form>
  </div>
</template>
<script>
export default {
  name: "LoginIn",
  data() {
    return {
      captchaUrl: "",
      loginForm: {
        username: "",
        password: "",
      },
      checked: false,
      loginFormRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 10 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          ,
          { min: 6, message: "密码长度要大于6", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    turnTo() {
      this.$router.push("/main");
    },
    turnToRegister() {
      this.$router.push("/register");
    },
    turnToPersonalCenter() {
      this.$router.push("/PersonalCenter");
    },
    submitLogin() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        // console.log(this.loginForm.username + this.loginForm.password);
        const { data: res } = await this.$http.post(`/user/login`, {
          username: this.loginForm.username,
          password: this.loginForm.password,
          isAdmin: true,
        });
        // console.log(res);
        if (res.code !== 0) {
          return this.$message({
            type: "error",
            message: "登录失败！",
            duration: 3000,
          });
        }
        window.localStorage.setItem("token", res.token);
        // window.localStorage.setItem("token", JSON.parse(res.token));
        // console.log(res.value);
        this.$message.success("登录成功！");
        this.$router.push("/Home");
        // this.$router.push("/Main");
      });
    },
  },
};
</script>
<style scoped>
/* 头部 */
.header {
  width: 100%;
  height: 100px;
  border-bottom: 1px solid gray;
}
/* 表单 */
.loginContainer {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 100px auto;
  width: 350px;
  padding: 15px 35px 15px 35px;
  background: aliceblue;
  border: 1px solid blueviolet;
  box-shadow: 0 0 25px #f885ff;
}
.loginTitle {
  margin: 0px auto 48px auto;
  text-align: center;
  font-size: 40px;
}
.loginRemember {
  text-align: left;
  margin: 0px 0px 15px 0px;
}
.register {
  width: 100%;
  height: 38px;
  margin-top: 20px;
  padding-right: 0;
  line-height: 38px;
  color: red;
}
.register:hover {
  text-decoration: underline;
}
/* 灯笼 */
:root {
  --lineColor: #ecaa2f;
  --bg: #f00;
}

.container1 {
  float: left;
  width: 200px;
  height: 150px;
  position: relative;
  animation: rotate 3s infinite ease-in-out;
}

.container2 {
  float: right;
  width: 200px;
  height: 150px;
  position: relative;
  animation: rotate 3s infinite ease-in-out;
}

.center {
  position: relative;
  width: 100%;
  height: 100%;
  background: var(--bg);
  border-radius: 120px;
  box-shadow: 0 0 80px -10px var(--bg);
  animation: rotate 3s infinite ease-in-out;
  transform-origin: top center;
}

.center::before {
  content: "";
  position: absolute;
  top: -8px;
  left: calc(50% - 40px);
  width: 80px;
  height: 10px;
  background: var(--lineColor);
  border-radius: 5px 5px 0 0;
  z-index: 2;
}

.center::after {
  content: "";
  width: 80px;
  height: 10px;
  background: var(--lineColor);
  border-radius: 0 0 5px 5px;
  position: absolute;
  bottom: -8px;
  left: calc(50% - 40px);
  z-index: 2;
}

.center-line {
  width: 100%;
  height: 100%;
}

.center-line span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 18px;
  font-size: 18px;
  color: var(--lineColor);
  font-weight: bold;
}

.center-line::before {
  content: "";
  position: absolute;
  top: 0;
  left: calc(50% - 75px);
  width: 150px;
  height: 150px;
  border: 2px solid var(--lineColor);
  border-radius: 50%;
}

.center-line::after {
  content: "";
  position: absolute;
  top: 0;
  left: calc(50% - 35px);
  width: 70px;
  height: 150px;
  border: 2px solid var(--lineColor);
  border-radius: 50%;
}

.head-line {
  position: absolute;
  left: calc(50% - 2px);
  top: -60px;
  width: 4px;
  height: 60px;
  background-color: var(--lineColor);
}

.footer-line {
  position: absolute;
  left: calc(50% - 2px);
  bottom: -50px;
  width: 4px;
  height: 50px;
  background-color: var(--lineColor);
  animation: rotate 3s infinite ease-in-out;
}

.footer-line::after {
  content: "";
  position: absolute;
  bottom: -75px;
  left: calc(50% - 8px);
  width: 16px;
  height: 80px;
  background: linear-gradient(
    #f00,
    #e36d00 3px,
    #fbd342 5px,
    #e36d00 8px,
    #e36d00 12px,
    #f00 16px,
    rgba(255, 0, 0, 0.8) 26px,
    rgba(255, 0, 0, 0.6)
  );
  border-radius: 5px 5px 0 0;
}

@keyframes rotate {
  0%,
  100% {
    transform: rotate(-10deg);
  }

  50% {
    transform: rotate(10deg);
  }
}
</style>