// 完成
<template>
  <div class="index">
    <h2>演出计划管理</h2>
    <!-- 查询 -->
    <div class="search">
      <div class="inp">
        <el-form :inline="true" label-position="left">
          <el-form-item label="剧目名称:" prop="searchName">
            <el-input v-model="searchName"></el-input>
          </el-form-item>
          <el-button @click="search()">查询</el-button>
          <el-button @click="insert()">增加</el-button>
          <div class="clear"></div>
        </el-form>
      </div>
    </div>
    <!-- 展示 -->
    <div class="show">
      <h3>演出计划展示</h3>
      <table>
        <thead>
          <tr>
            <th>演出计划编号</th>
            <th>剧目名称</th>
            <th>语言类型</th>
            <th>在映时间</th>
            <th>演出厅名称</th>
            <th>基础票价</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in this.planDate" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ item.video }}</td>
            <td>{{ item.language }}</td>
            <td>{{ item.releaseTime }}</td>
            <td>{{ item.studio }}</td>
            <td>{{ item.price }}</td>
            <td>
              <button @click="editPlan(item)">修改</button>
              <button @click="deletePlan(item)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- 添加 -->
    <div class="addForm">
      <el-form
        v-show="addPlan"
        :model="selectedPlan"
        status-icon
        label-width="100px"
      >
        <h3 class="Title">添加演出计划信息</h3>
        <el-form-item label="剧目名称" prop="selectedPlan.video">
          <el-input v-model="selectedPlan.video"></el-input>
        </el-form-item>
        <el-form-item label="语言类型" prop="selectedPlan.language">
          <el-input v-model="selectedPlan.language"></el-input>
        </el-form-item>
        <el-form-item label="在映时间" prop="selectedPlan.releaseTime">
          <el-input v-model="selectedPlan.releaseTime"></el-input>
        </el-form-item>
        <el-form-item label="安排的演出厅名称" prop="selectedPlan.studio">
          <el-input v-model="selectedPlan.studio"></el-input>
        </el-form-item>
        <el-form-item label="基础票价" prop="selectedPlan.price">
          <el-input v-model="selectedPlan.price"></el-input>
        </el-form-item>
        <el-button type="primary" @click="submitAdd()">提交添加</el-button>
        <el-button type="primary" @click="cancleAdd()">取消</el-button>
      </el-form>
    </div>
    <!-- 修改 -->
    <div class="updateForm">
      <el-form
        v-show="updatePlan"
        :model="selectedPlan"
        status-icon
        label-width="100px"
      >
        <h3 class="Title">修改演出计划信息</h3>
        <el-form-item label="演出计划id" prop="selectedPlan.id">
          <el-input v-model="selectedPlan.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="剧目名称" prop="selectedPlan.video">
          <el-input v-model="selectedPlan.video" disabled></el-input>
        </el-form-item>
        <el-form-item label="语言类型" prop="selectedStudio.language">
          <el-input v-model="selectedPlan.language"></el-input>
        </el-form-item>
        <el-form-item label="在映时间" prop="selectedPlan.releaseTime">
          <el-input v-model="selectedPlan.releaseTime"></el-input>
        </el-form-item>
        <el-form-item label="安排的演出厅" prop="selectedPlan.studio">
          <el-input v-model="selectedPlan.studio" disabled></el-input>
        </el-form-item>
        <el-form-item label="基础票价" prop="selectedPlan.price">
          <el-input v-model="selectedPlan.price"></el-input>
        </el-form-item>
        <el-button type="primary" @click="savePlan()">提交修改</el-button>
        <el-button type="primary" @click="cancleSave()">取消</el-button>
      </el-form>
    </div>
    <div class="clear"></div>
  </div>
</template>
<script>
export default {
  name: "PerformancePlan",
  data() {
    return {
      searchName: "",
      planDate: [],
      updatePlan: false,
      addPlan: false,
      selectedPlan: {
        id: "",
        video: "",
        language: "",
        releaseTime: "",
        studio: "",
        price: "",
      },
    };
  },
  async created() {
    const { data: res } = await this.$http.get("/plan/getPlan");
    console.log(res);
    this.planDate = res.value;
  },
  methods: {
    async search() {
      const { data: res } = await this.$http.get("/plan/getPlanByVideoName", {
        params: {
          name: this.searchName,
        },
      });
      console.log(res);
      this.planDate = res.value;
    },
    // 显示添加表单
    async insert() {
      this.addPlan = true;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "airblue";
      a.style.border = "1px solid blueviolet";
      a.style.boxShadow = "0 0 25px #f885ff";
    },
    // 提交添加修改
    async submitAdd() {
      // 请求
      const { data: res } = await this.$http.post(`/plan/insertPlan`, {
        token: window.localStorage.getItem("token"),
        value: {
          video: this.selectedPlan.video,
          studio: this.selectedPlan.studio,
          price: this.selectedPlan.price,
        },
        time: this.selectedPlan.releaseTime,
      });
      console.log(res);
      // 插入后知更新
      const { data: rest } = await this.$http.get("/plan/getPlan");
      // console.log(res);
      this.planDate = rest.value;
      this.addPlan = false;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    cancleAdd() {
      this.addPlan = false;
      var a = document.getElementsByClassName("addForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    // 更新表单可见
    async editPlan(item) {
      this.updatePlan = true;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "airblue";
      a.style.border = "1px solid blueviolet";
      a.style.boxShadow = "0 0 25px #f885ff";
      this.selectedPlan = item;
    },
    //提交更新表单
    async savePlan() {
      const { data: res } = await this.$http.post(`/plan/updatePlan`, {
        token: window.localStorage.getItem("token"),
        value: {
          id: this.selectedPlan.id,
          video: this.selectedPlan.video,
          releaseTime: this.selectedPlan.releaseTime,
          price: this.selectedPlan.price,
          language: this.selectedPlan.language,
        },
      });
      const { data: rest } = await this.$http.get("/plan/getPlan");
      console.log("更新表单");
      console.log(rest);
      this.planDate = rest.value;
      console.log("数据");
      console.log(this.planDate);
      this.updatePlan = false;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
    async deletePlan(item) {
      this.selectedPlan = item;
      const { data: res } = await this.$http.get("/plan/deletePlan", {
        params: {
          id: this.selectedPlan.id,
          token: window.localStorage.getItem("token"),
        },
      });
      const { data: rest } = await this.$http.get("/plan/getPlan");
      console.log(res);
      this.planDate = rest.value;
    },
    cancleSave() {
      this.updatePlan = false;
      var a = document.getElementsByClassName("updateForm")[0];
      a.style.backgroundColor = "#e9eef3";
      a.style.border = "none";
      a.style.boxShadow = "0 0 0 #e9eef3";
    },
  },
};
</script>
<style scoped>
.index {
  padding: 20px;
  height: 100%;
}
h2 {
  text-align: center;
}
.search .el-form-item {
  float: left;
  width: 200px;
}
.search .el-button {
  float: left;
  width: 60px;
  text-align: center;
  border-radius: 5px;
  margin-top: 40px;
}
.show {
  width: 60%;
  margin: 0 auto;
}
.show table tr {
  width: 100%;
}
.show table th,
.show table td {
  text-align: center;
  width: 100px;
  height: 40px;
}
.show table td button {
  width: 48px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  margin-right: 2px;
}
/* 添加表单 */
.addForm {
  float: left;
  width: 350px;
  height: 500px;
  margin: 10px 100px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
}
/* 修改表单 */
.updateForm {
  float: right;
  width: 350px;
  height: 500px;
  margin: 10px 100px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 15px 35px 15px 35px;
  box-sizing: border-box;
}
.el-button {
  float: left;
  width: 100px;
  text-align: center;
  border-radius: 5px;
  margin: 10px 30px 0 0;
}
</style>