// 完成
<template>
  <div style="width: 100%">
    <div class="top">
      <div class="left"><img :src="this.movieDetails.imgUrl" alt="图片" /></div>
      <div class="middle">
        <div class="name">{{ this.movieDetails.name }}</div>
        <div class="type">{{ this.movieDetails.type }}</div>
        <div class="source">
          {{ this.movieDetails.source }}&nbsp;&nbsp;/&nbsp;&nbsp;
        </div>
        <div class="duration">{{ this.movieDetails.duration }}</div>
        <div class="clear"></div>
        <div class="time">
          {{ this.movieDetails.releaseTime }}&nbsp;&nbsp;在天幕影院上映
        </div>
        <div class="wannaSee">想看</div>
        <div class="mark">评分</div>
        <div class="clear"></div>
        <div class="ticketing">特惠购票</div>
      </div>
      <div class="right">
        {{ this.movieDetails.description }}
      </div>
    </div>
    <div class="movieInformation">
      <div class="time">
        <div class="title">观影时间:&nbsp;</div>
        <div class="timeMD">
          今天{{ this.time.month }}月{{ this.time.day }}日
        </div>
        <div class="clear"></div>
      </div>
      <!-- 演出计划表格 -->
      <div class="table">
        <table>
          <thead>
            <tr>
              <th>放映时间</th>
              <th>语言版本</th>
              <th>放映厅</th>
              <th>售价</th>
              <th>选座购票</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in tableData" :key="index">
              <td>{{ item.releaseTime }}</td>
              <td>{{ item.language }}</td>
              <td>{{ item.studio }}</td>
              <td>{{ item.price }}</td>
              <td @click="turnToSeatSelection(item)">选座购票</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "MovieDetails",
  data() {
    return {
      time: {
        month: "",
        day: "",
      },
      tableData: [],
    };
  },
  async created() {
    let temp = new Date();
    this.time.month = temp.getMonth() + 1;
    this.time.day = temp.getDate();
    // 获取剧目信息
    this.movieDetails = JSON.parse(window.localStorage.getItem("movieDetails"));
    let temp1 = "";
    temp1 +=
      new Date(this.movieDetails.releaseTime).getMonth() + 1 > 10
        ? new Date(this.movieDetails.releaseTime).getMonth() + 1 + "月"
        : "0" + (new Date(this.movieDetails.releaseTime).getMonth() + 1) + "月";
    temp1 +=
      new Date(this.movieDetails.releaseTime).getDate() > 10
        ? new Date(this.movieDetails.releaseTime).getDate() + "日"
        : "0" + new Date(this.movieDetails.releaseTime).getDate() + "日";
    this.movieDetails.releaseTime = temp1;
    // console.log(this.movieDetails);
    let temp3 = "";
    let temp2 = new Date(this.movieDetails.time);
    temp3 += temp2.getFullYear() + "-";
    temp3 += temp2.getMonth() + 1 + "-";
    temp3 += temp2.getDate() + " ";
    temp3 += temp2.getHours() + ":";
    temp3 += temp2.getMinutes();
    this.movieDetails.time = temp3;
    // 演出计划请求
    // console.log(this.movieDetails.id);
    const { data: data } = await this.$http.get(`/plan/getPlanByVideoId`, {
      params: {
        videoId: this.movieDetails.id,
        time: "2023-06-07 12:12:12",
      },
    });
    this.tableData = data.value;
    console.log(data);
    // 将时间戳转化为月日
    for (let i = 0; i < this.tableData.length; i++) {
      let temp = "";
      temp +=
        new Date(this.tableData[i].releaseTime).getMonth() + 1 > 10
          ? new Date(this.tableData[i].releaseTime).getMonth() + 1 + "月"
          : "0" +
            (new Date(this.tableData[i].releaseTime).getMonth() + 1) +
            "月";
      temp +=
        new Date(this.tableData[i].releaseTime).getDate() > 10
          ? new Date(this.tableData[i].releaseTime).getDate() + "日"
          : "0" + new Date(this.tableData[i].releaseTime).getDate() + "日";
      this.tableData[i].releaseTime = temp;
    }
    // 将演出计划的放映时间转化为XX: XX;
    // for (let i = 0; i < this.tableData.length; i++) {
    //   let time = "";
    //   time +=
    //     new Date(this.tableData[i].time).getHours() < 10
    //       ? "0" + new Date(this.tableData[i].time).getHours() + ":"
    //       : new Date(this.tableData[i].time).getHours() + ":";
    //   time +=
    //     new Date(this.tableData[i].time).getMinutes() < 10
    //       ? "0" + new Date(this.tableData[i].time).getMinutes()
    //       : new Date(this.tableData[i].time).getMinutes();
    //   this.tableData[i].time = time;
    // }
  },
  methods: {
    turnToSeatSelection(item) {
      window.localStorage.setItem("planData", JSON.stringify(item));
      this.$router.push("/SeatSelection");
    },
  },
};
</script>
